import React from 'react';

class App extends React.Component{
	constructor(props){
		super(props);
		this.state = {
			data: [],
			currentQuote: ''
		}
	}

	componentDidMount(){
		fetch('https://type.fit/api/quotes')
		.then(response => response.json())
		.then(result => {
			this.setState({
				data: result,
				currentQuote: result[Math.floor(Math.random() * 1643)]
			});
		})
		.catch((error) => console.log(error));
	}

	handleClick = () => {
		this.setState({
			currentQuote: this.state.data[Math.floor(Math.random() * 1643)]
		})
	}

	render(){

		return (
			<div id='quote-box'>
				<p id='text'><i className="fas fa-quote-left"></i>{this.state.currentQuote
					? this.state.currentQuote.text
					: "Wait..."}</p>
				<p id='author'>-{this.state.currentQuote
					? this.state.currentQuote.author
					: ''}</p>
				<div>
					<a href="twitter.com/intent/tweet" id='tweet-quote' target="_blank"><i className="fab fa-twitter"></i></a>
					<button id="new-quote" onClick={this.handleClick}>New Quote</button>
				</div>
			</div>
		)
	}
}

export default App;